import React from 'react';
import './Modal.scss';

const Modal = ({ isOpen, close, title, language, url, text, text2, src}) => {

    return (
      <>{ isOpen ?
      <React.Fragment>
     	  <div className="Modal-overlay" onClick={close}/>
        <div className="Modal">
          <div className="closeicon">
            <i onClick={close}>
              <span></span>
              <span></span>
            </i>
          </div>
          <p className="title">{title}</p>
          <p className="url"><a href={url}>{url}</a></p>
          <p className="language">사용기술 : {language}</p>
          <div className="content">
            <p>- {text}</p>
            <p>- {text2}</p>
            <div className="img-list">
              <img src={src} alt={title} />
            </div>
          </div>

        </div>
      </React.Fragment>
      : null }</>
    )

}
export default Modal;
